import { Box, Button, Card, CardActions, CardContent, CardMedia, Typography } from "@mui/material";
import { useEffect } from "react";
import { connect } from "react-redux";
import { useParams } from "react-router-dom";
import Header from "../../components/Header";
import { documentPathTypes } from "../../configs/constant";
import { getToken } from "../../store/actions/walletActions";

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { solid } from '@fortawesome/fontawesome-svg-core/import.macro' // <-- import styles to be used

import './Token.scss';
import { formatDate } from "../../utils/helpers";
import TokenGallery from "./TokenGallery";
import TokenDocuments from "./TokenDocuments";

const Token = props => {
  const { tokens, getToken } = props;
  const { id } = useParams();
  const token = tokens.find(item => item.token.uuid === id);

  useEffect(() => {
    !!id && getToken(id);
  }, [id])

  if ( token ) {
    const coverImage = token.documents.find(
      (document) => document.path === documentPathTypes.COVER_IMAGE
    );
    return (
      <div className="site token-single">
        <Header />

        <section className="token__information">
          <div className="cover-image">
            <img src={coverImage.fileUrl} alt={token.token.name} />
          </div>
          <div className="container flex flex--vertical">
            <Box className="description" >
              <Typography className="title" gutterBottom variant="h5" component="div">
                {token.token.name}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                {token.token.description}
              </Typography>
            </Box>

            <TokenGallery documents={token?.documents.filter(item => item.path === documentPathTypes.GALLERY )} />
            
            <Box className="specifications"> 
              <Typography className="title" gutterBottom variant="h5" component="div">
                <FontAwesomeIcon icon={solid('file-invoice-dollar')} /> Specifications</Typography>
              
              <Typography variant="body2" color="text.secondary">
                <Typography component="span" variant="subtitle2">Digital Twin Origin: </Typography> John Doe Auction House
              </Typography>
              <Typography variant="body2" color="text.secondary">
                <Typography component="span" variant="subtitle2">Creation Date: </Typography> {formatDate(token.token.creationDate)}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                <Typography component="span" variant="subtitle2">Price: </Typography> {token?.token.value} {token?.token.currency}
              </Typography>
            </Box>

            <TokenDocuments documents={token?.documents} /> 
          </div>
        </section>
      </div>
    )
  }

  return <>..</>

}

export default connect(state => {
  let data = state?.wallet
  return {
    tokens: data.tokens || []
  }
}, dispatch => {
  return {
    getToken: (id) => {
      dispatch(getToken({ id }));
    }
  }
})(Token);