import { createAsyncThunk } from '@reduxjs/toolkit';
import { default as apiConfigs } from '../../configs/api';

export const getWallet = createAsyncThunk('wallet/info', async (payload, {getState, rejectWithValue}) => {
  const auth = getState()?.auth;
  if ( !auth ) {
    return rejectWithValue({
      code: 'get-wallet',
      message: 'Permission denied'
    });
  }

  try {
    const response = await fetch(apiConfigs.wallet.info, {
      method: 'GET',
      headers: {
        'Authorization': `Bearer ${auth.accessToken}`
      }
    }).then(response => response.json())

    if ( !response.status || response.status !== 'success' || !response.result ) {
      return rejectWithValue({
        code: 'get-wallet',
        message: 'Permission denied'
      })
    }
    const publicInfo = response.result?.publicInfo || {};
    const privateInfo = response.result?.privateInfo || {};

    return {
      public: publicInfo,
      private: {
        phone: {
          number: privateInfo?.phoneNumber.phoneNumber,
          dialCode: privateInfo?.dialCode.dialCode,
          verified: privateInfo?.phoneNumber.verified,
        },
        email: {
          address: privateInfo?.email.emailAddress,
          verified: privateInfo?.email.verified,
        },
        username: privateInfo?.username
      },
      address: response.result?.address
    }
  } catch ( error ) {
    return rejectWithValue({
      code: 'get-wallet',
      message: error.getMessage()
    })
  }

  
});

export const getTokenList = createAsyncThunk('wallet/token-list', async (payload, {getState, rejectWithValue}) => {
  const auth = getState()?.auth;
  if ( !auth ) {
    return rejectWithValue({
      code: 'get-token-list',
      message: 'Permission denied'
    });
  }
  try {
    const response = await fetch(apiConfigs.wallet.token, {
      method: 'GET',
      headers: {
        'Authorization': `Bearer ${auth.accessToken}`
      }
    }).then(response => response.json())

    if ( !response.status || response.status !== 'success' || !response.result ) {
      return rejectWithValue({
        code: 'get-token-list',
        message: 'Permission denied'
      })
    }
    return {
      tokens: response.result
    } 

  } catch (error) {
    return rejectWithValue({
      code: 'get-tokken-list',
      message: 'Something went wrong please contact developer!'
    });
  }
});

export const getToken = createAsyncThunk('wallet/token', async(payload, {getState, rejectWithValue}) => {
  const auth = getState()?.auth;
  if ( !auth ) {
    return rejectWithValue({
      code: 'get-token-list',
      message: 'Permission denied'
    });
  }

  const { id } = payload
  if ( !id ) {
    return rejectWithValue({
      code: 'get-token',
      message: 'Missing token id'
    })
  }
  try {
    const response = await fetch(`${apiConfigs.wallet.token}/${id}`, {
      method: 'GET',
      headers: {
        'Authorization': `Bearer ${auth.accessToken}`
      }
    }).then(response => response.json())

    if ( !response.status || response.status !== 'success' || !response.result ) {
      return rejectWithValue({
        code: 'get-token',
        message: response.result || 'Permission denied'
      })
    }
    
    return { token: response.result };

  } catch (error) {
    return rejectWithValue({
      code: 'get-tokken-list',
      message: 'Something went wrong please contact developer!'
    });
  }
})