import { Navigate } from "react-router-dom";
import { connect } from "react-redux";

import { getAuthToken, refreshToken } from "../../store/actions/authActions";
import Loader from "../Loader";
import { getWallet } from "../../store/actions/walletActions";

const RequireUser = props => {
  const { accessToken, refreshToken, expiredAt, children, getWallet } = props;

  let current = (new Date().getTime())

  if ( !!accessToken ) {
    if ( expiredAt < current && !!refreshToken ) {
      refreshToken();
      return <Loader />; 
    }
  } else {
    return <Navigate to="/token-login" />;
  }

  // call get user information right here 
  getWallet();

  return children;
};

export default connect(state => {
  let auth = state.auth

  return {
    accessToken: auth.accessToken,
    expiredAt: auth.expiredAt,
    refreshToken: auth.refreshToken
  }
}, dispatch => {
  return {
    getToken: () => {
      dispatch(getAuthToken());
    },
    refreshToken: () => {
      dispatch(refreshToken());
    },
    getWallet: () => {
      dispatch(getWallet());
    }
  }
})(RequireUser);

