const Item = (props) => {
  const { title, text, index } = props;
  const [open, setOpen] = useState(index===0);
  const handleClick = () => setOpen(!open);
  return (
    <>
      <ListItemButton 
        sx={{ borderTop: '1px solid var(--unikbase-gray)', pl: 0 }} 
        className="site__faq-item" onClick={handleClick}>
          <ListItemText primary={title} style={{ textTransform: 'uppercase' }} />
          {open ? '-' : '+'}
      </ListItemButton>
      <Collapse in={open} timeout="auto" unmountOnExit style={{
        paddingLeft: '2em',
        paddingBottom: '2em'
      }}>
        <div dangerouslySetInnerHTML={{__html: text}}></div>
      </Collapse>
    </>
  )
}

