import { Card, Box, CardContent, Typography, CardMedia, Skeleton } from "@mui/material";
import { connect } from "react-redux";
import { redirect, useNavigate } from "react-router-dom";
import { documentPathTypes } from "../../configs/constant";
import './TokenList.scss';

const ItemPlaceholder = () => {
  return (
    <Card sx={{ mt: 1 }} className="flex flex--align-center">
      <CardMedia
        component="div"
      >
        <Skeleton sx={{ bgcolor: 'var(--unikbase-gray)' }} variant="rounded" width={100} height={120} />
      </CardMedia>
      <CardContent>
        <Typography sx={{ mb: 1 }} component="div" variant="h5">
          <Skeleton sx={{ bgcolor: 'var(--unikbase-gray)' }} variant="rounded" width={210} height={30} />
        </Typography>
        <Typography variant="subtitle1" color="text.secondary" component="div">
          <Skeleton sx={{ bgcolor: 'var(--unikbase-gray)' }} variant="rounded" width={400} height={60} />
        </Typography>
      </CardContent>
    </Card>
  )
}

const Item = ({ name, description, image, id }) => {
  const navigate = useNavigate();
  const showItem = () => {
    return navigate(`/token/${id}`);
  }
  return (
    <Card onClick={() => showItem()} sx={{ mt: 1 }} className="token flex  flex--nowrap">
      <CardMedia
        component="img"
        image={image}
        alt={name}
      />
      <CardContent>
        <Typography component="div" variant="h5">
          {name}
        </Typography>
        <Typography variant="subtitle1" color="text.secondary" component="div">
          {description}
        </Typography>
      </CardContent>
    </Card>
  )
}

const TokenList = props => {
  const { tokensFetching, tokens } = props

  return (
    <Box sx={{ mt: 2 }} className="tokens">
      {tokensFetching !== 'success' ?
        <>
          <ItemPlaceholder />
          <ItemPlaceholder />
        </>
        : (tokens.length > 0 && tokens.map(item => {
          let token = item.token;
          const coverImage = item.documents.find(
            (document) => document.path === documentPathTypes.COVER_IMAGE
          );
          
          return <Item key={token.uuid} id={token.uuid} title={token.name} description={token.description} image={coverImage.fileUrl} />
        }))
      }
    </Box>
  )
}

export default connect(state => {
  let data = state?.wallet;
  return {
    tokens: data.tokens,
    tokensFetching: data.tokensFetching,
  }
})(TokenList);