import { Avatar, Box, IconButton, InputAdornment, OutlinedInput, Skeleton, TextField, Typography } from "@mui/material";
import { useEffect, useState } from "react";
import { connect } from "react-redux";
import Header from "../../components/Header";
import { TokenList } from "../../components/Token";

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { solid } from '@fortawesome/fontawesome-svg-core/import.macro' // <-- import styles to be used


import './Wallet.scss';
import { getTokenList } from "../../store/actions/walletActions";

const Wallet = props => {
  const { publicInformation, address, getTokens } = props
  const [editing, startEdit] = useState(false);

  const copyToClipBoard = () => {
    window.navigator.clipboard.writeText(address);
  }
  useEffect(() => {
    getTokens()
  }, [])

  return (
    <div className="site wallet">
      <Header />
      <section className="main">
        <div className="container">
          
          <Box sx={{ mt: 5 }} className="wallet__overview flex flex--nowrap">
            <div className="avatar">
              {!!publicInformation && publicInformation.base64Avatar ? 
                <Avatar className="wallet__image" src={`data:image/png;base64,${publicInformation.base64Avatar}`} alt="avatar" /> 
                : <Skeleton sx={{ bgcolor: 'var(--unikbase-gray)' }} variant="circular" width={100} height={100} />
              }
            </div>

            <div className="description flex flex--vertical">
              {!!publicInformation && publicInformation.description ?
                (editing ? 
                  <OutlinedInput placeholder="Please enter text" className="description__editor" defaultValue={publicInformation.description} />
                  : <Box className="description__content">{publicInformation.description}</Box>)
                // Skeleton for loading
                : <Skeleton sx={{ bgcolor: 'var(--unikbase-gray)' }} variant="rounded" width={210} height={60} />
              }
              
              <OutlinedInput
                id="wallet-address"
                endAdornment={
                  <IconButton onClick={() => copyToClipBoard()} sx={{ fontSize: '12px' }}>
                    <FontAwesomeIcon icon={solid('copy')} />
                  </IconButton>}
                aria-describedby="wallet-address"
                inputProps={{
                  'aria-label': 'wallet-address',
                }}
                fullWidth
                className="m--t"
                disabled
                size="small"
                value={address}
              />
              
            </div>
          </Box>

          <Box className="wallet__tokens" sx={{ mt: 5 }}>
            <Typography variant="h5">My digital twins</Typography>

            <TokenList />
          </Box>
        </div>
      </section>
    </div>
  )
}

export default connect(state => {
  let wallet = state?.wallet
  return {
    publicInformation: wallet?.public,
    address: wallet?.address
  }
}, dispatch => {
  return {
    getTokens: () => {
      dispatch(getTokenList())
    }
  }
})(Wallet);