import { Box, Card, CardContent, FormControl, FormControlLabel, FormGroup, FormLabel, Radio, RadioGroup, Tab, Tabs, Typography } from "@mui/material";
import { useState } from "react";
import { connect } from "react-redux";
import Header from "../../components/Header";
import { hideEmail } from "../../utils/helpers";

function a11yProps(index) {
  return {
    id: `vertical-tab-${index}`,
    'aria-controls': `vertical-tabpanel-${index}`,
    sx: {
      pt: 1,
      pb: 1,
      pl: 0,
      textTransform: 'none',
      alignItems: 'flex-start'
    }
  };
}

const TabPanel = props => {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ pl: 3 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

const Profile = props => {
  const [tab, setTab] = useState(0);
  const { privateInformation } = props
  
  const handleChange = (event, newValue) => {
    setTab(newValue);
  };
  
  return (
    <div className="site profile single">
      <Header />

      <div className="profile__setings">
        <div className="container flex flex--nowrap">
          <Card sx={{ width: '100%' }}>
            <CardContent>
              <Typography sx={{ mb: 2 }} variant="h4">Contact info</Typography>

              <Box sx={{ mb: 3 }} className="contact__informations">
                <Typography sx={{ mb: 1 }} variant="body1">
                  <Typography variant="subtitle2">Username</Typography>
                  {privateInformation.username} 
                </Typography>
                <Typography sx={{ mb: 1 }} variant="body1">
                  <Typography variant="subtitle2">Phone</Typography>
                  {privateInformation.phone.number} 
                </Typography>
                <Typography sx={{ mb: 1 }} variant="body1">
                  <Typography variant="subtitle2">Email</Typography>
                  {hideEmail(privateInformation.email.address)} 
                </Typography>
              </Box>

              <Typography sx={{ mb: 2 }} variant="h4">Settings</Typography>
              <Box
                  sx={{ flexGrow: 1, bgcolor: 'background.paper', display: 'flex', height: 224 }}
                >
                  <Tabs
                    orientation="vertical"
                    variant="scrollable"
                    value={tab}
                    onChange={handleChange}
                  >
                    <Tab label="Languages" {...a11yProps(0)} />
                    <Tab label="Change password" {...a11yProps(1)} />
                  </Tabs>
                  <TabPanel value={tab} index={0}>
                    <FormControl>
                      <FormLabel id="language-setting-label">Language</FormLabel>
                      <RadioGroup
                        aria-labelledby="language-setting"
                        defaultValue="en"
                        name="language-setting"
                      >
                        <FormControlLabel value="en" control={<Radio />} label="English" />
                        <FormControlLabel value="fr" control={<Radio />} label="French" />
                      </RadioGroup>
                    </FormControl>
                  </TabPanel>
                  <TabPanel value={tab} index={1}>
                    Item Two
                  </TabPanel>
                  
                </Box>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  )
}

export default connect(state => {
  let data = state?.wallet;
  return {
    privateInformation: data.private
  }
})(Profile);