import { createSlice } from "@reduxjs/toolkit";
import { getWallet, getTokenList, getToken } from "../actions/walletActions";

const initialState = {
  status: 'idle',
  error: null,
  public: null,
  private: {
    phone: {
      number: null,
      dialCode: 0,
      verified: false,
    },
    email: {
      address: null,
      verified: false,
    },
    username: null
  },
  address: '',
  tokensFetching: 'idle',
  tokens: []
}


const walletSlice = createSlice({
  name: "profile",
  initialState: initialState,
  reducers: {
  },
  extraReducers: builder => {
    builder
      .addCase(getWallet.pending, (state, action) => {
        state.status = 'loading'
      })
      .addCase(getWallet.fulfilled, (state, action) => {
        const data = action.payload;
        state.status = 'success';
        state.public = data.public;
        state.private = data.private;
        state.address = data.address;
        state.error = null;
      })
      .addCase(getWallet.rejected, (state, action) => {
        state.status = 'failed';
        state.error = action.payload;
        state.public = initialState.public;
        state.private = initialState.private;
        state.address = initialState.address;
      })

    builder
      .addCase(getTokenList.pending, (state, action) => {
        state.tokensFetching = 'loading'
      })
      .addCase(getTokenList.fulfilled, (state, action) => {
        const data = action.payload;
        state.tokensFetching = 'success';
        state.tokens = data.tokens;
        state.error = null;
      })
      .addCase(getTokenList.rejected, (state, action) => {
        state.tokensFetching = 'failed';
        state.error = action.payload;
      })


    builder
      .addCase(getToken.pending, (state, action) => {
        state.tokensFetching = 'loading'
      })
      .addCase(getToken.fulfilled, (state, action) => {
        const data = action.payload;
        state.tokensFetching = 'success';
        state.error = null;
        const token = data.token;
        let tokens = state.tokens || [];
        let index = tokens.findIndex(item => item.token.uuid === token.token.uuid)
        if ( index >= 0 ) {
          tokens[index] = token;
        } else {
          tokens.push(token);
        }
        state.tokens = tokens;
      })
      .addCase(getToken.rejected, (state, action) => {
        state.tokensFetching = 'failed';
        state.error = action.payload;
      })
  }
});

// export const { logout } = authSlice.actions;
export default walletSlice.reducer;