import { createSlice } from "@reduxjs/toolkit";
import { getAuthToken, refreshToken } from "../actions/authActions";

const initialState = {
  status: 'idle',
  error: null,
  accessToken: null,
  refreshToken: null,
  expiredAt: 0,
}

const authSlice = createSlice({
  name: "auth",
  initialState: initialState,
  reducers: {
    logout(state, action) {
      state.accessToken = "";
      state.refreshToken = "";
      state.expiredAt = 0;
      state.status = 'idle';
      state.error = null;
    },
  },
  extraReducers: builder => {
    builder
      .addCase(getAuthToken.pending, (state, action) => {
        state.status = 'loading'
      })
      .addCase(getAuthToken.fulfilled, (state, action) => {
        const data = action.payload;
        state.status = 'success';
        state.accessToken = data.accessToken;
        state.refreshToken = data.refreshToken;
        state.expiredAt = data.expiredAt;
        state.error = null;
      })
      .addCase(getAuthToken.rejected, (state, action) => {
        state.status = 'failed';
        state.error = action.payload;
        state.accessToken = null;
        state.refreshToken = null;
        state.expiredAt = null;
      })
    
    builder
      .addCase(refreshToken.pending, (state, action) => {
        state.status = 'loading'
      })
      .addCase(refreshToken.fulfilled, (state, action) => {
        const data = action.payload;
        state.status = 'success';
        state.accessToken = data.accessToken;
        state.expiredAt = data.expiredAt;
        state.error = null;
      })
      .addCase(refreshToken.rejected, (state, action) => {
        state.status = 'failed';
        state.error = action.payload;
        state.accessToken = null;
        state.refreshToken = null;
        state.expiredAt = null;
      })
  }
});

export const { logout } = authSlice.actions;
export default authSlice.reducer;