import React, { useState } from "react";
import Logo from "../../components/Logo";
import { default as LoginForm } from "../../components/Login";

import { Button, SwipeableDrawer, Typography } from '@mui/material';

import './Login.scss';
import { Box } from "@mui/system";
import { connect } from "react-redux";
import { Navigate } from "react-router-dom";

const Login = (props) => {
  const { isLoggedIn } = props
  const [open, openLoginForm] = useState(false);

  if ( isLoggedIn ) {
    return <Navigate to="/" />;
  }

  const toggleDrawer = (newState) => () => {
    openLoginForm(!!newState);
  }
  const container = document.getElementById('root');
  return (
    <div className="site login flex flex--vertical flex--align-center">

      <div className="site__header layer--top">
        <div className="container flex align--center">
          <Logo style={{ height: '100px'}} />
        </div>
      </div>

      <div className="description container align--center">
        <Typography variant="h5">Welcome to Unikbase</Typography>
        <Typography variant="caption">Copyright&copy; 2023 | <a href="https://unikbase.com" target="_blank" rel="noreferrer" title="Unikbase Homepage">unikbase.com</a></Typography>
      </div>

      <section className="login-actions">
        <div className="container align--center flex flex--vertical">
          <Button sx={{ mb: 1 }} color="gray" variant="contained">Create new account</Button>
          <Button sx={{ mb: 1 }} color="secondary" variant="contained" onClick={() => openLoginForm(true)}>Login account</Button>
        </div>
      </section>

    
      <SwipeableDrawer
        container={container}
        anchor="bottom"
        open={open}
        onClose={toggleDrawer(false)}
        onOpen={toggleDrawer(true)}
        disableSwipeToOpen={false}
        ModalProps={{ keepMounted: false }}
        className="login__modal"
        PaperProps={{ sx: {
            backgroundColor: 'transparent'
          } 
        }}
      >
        <Box className="container flex flex--vertical flex--align-center">
          <Typography color="#fff" sx={{ mb: 1 }} >login with usename</Typography>
          <Box
            className="container flex flex--vertical flex--align-center"
            sx={{
              backgroundColor: '#fff',
              p: 2,
              borderRadius: '8px 8px 0 0',
              minHeight: '50vh'
            }}
          >
            <LoginForm />
          </Box>
        </Box>
      </SwipeableDrawer>
    </div>
  )
}

export default connect(state => {
  let auth = state.auth;
  return {
    isLoggedIn: !!auth.accessToken
  }
})(Login); 