import { solid } from "@fortawesome/fontawesome-svg-core/import.macro"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { Typography } from "@mui/material"
import { Box } from "@mui/system"
import { Carousel } from 'react-responsive-carousel';
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader

import './TokenGallery.scss';

const TokenGallery = props => {
  const { documents } = props

  return (
    <Box className="gallery">
      <Typography className="title" gutterBottom variant="h5" component="div">
        <FontAwesomeIcon icon={solid('images')} />Gallery</Typography>
      <Typography variant="body2" color="text.secondary">
        <Carousel showThumbs={false} centerMode={true} centerSlidePercentage={70} >
          {documents.length > 0 && documents.map(item => (
            <div className="gallery__item" key={item.uuid}>
              <img src={item?.fileUrl} alt="gallery" />
            </div>
          ))}
        </Carousel>
      </Typography>
    </Box>
  )
}

export default TokenGallery;