import { Button } from "@mui/material";
import { redirect, useNavigate } from "react-router-dom";
import React from "react";
import Logo from "../Logo";

import './Header.scss';
import { connect } from "react-redux";
import { logout } from "../../store/slices/authSlice";
import { persistor } from "../../store/rootReducer";

const MenuItem = ({ link, title}) => {
  const navigate = useNavigate();

  const moveHandle = () => {
    return navigate(link)
  }
  return (
    <a href={link} onClick={(event) => {
      event.preventDefault();
      moveHandle();
    }} title={title}>{title}</a>
  )
}

const Header = ({ isLogged, userLogout }) => {
  const navigate = useNavigate();
  
  const logoutHandle = () => {
    userLogout();
    return navigate('/');
  }
  
  return (
    <header className="site__header">
      <div className="container container--fluid flex">
        <div className="brand">
          <Logo className="site__logo" />
        </div>

        <ul className="menu menu--horizontal site__navigation m--l">
          <li><MenuItem link="/profile" title="Profile" /></li>
          <li><MenuItem link="/" title="Wallet" /></li>
          <li><MenuItem link="/faq" title="FAQs" /></li>
          {!!isLogged && <li><a href="#logout" className="btn btn-logout" onClick={() => logoutHandle()}>Logout</a></li>}
        </ul>
      </div>
    </header>
  )
}

export default connect(state => {
  let data = state?.auth

  return {
    isLogged: !!(data.accessToken && data.expiredAt > (new Date().getTime()))
  }
}, dispatch => {
  return {
    userLogout: () => {
      persistor.purge();
      dispatch(logout());
    }
  }
})(Header);